
h2 {
    padding: 10px;
    margin: 20px 0px 0px 0px;
    border: 3px solid #777;
    font-size: 1.8em;
  }

.filter {
  border-radius: 3px;
  background-color: #eee;
  padding: 5px;
  margin: 5px;
  color: #444;
  text-align: left;
  display: inline-block;
  cursor: default;
}
text.xlabel {
    text-anchor: middle;
  }

text.ylabel {
    text-anchor: middle;
    alignment-baseline: central;
  }

.axis text {
    font-family: Courier;
    font-size: 1em;
  }
text.title {
    font-size: 14px;
    font-weight: bold;
    text-anchor: middle; }

text.label {
    font-size: 10px;
    fill: black;
    text-anchor: middle;}

.container {
    margin-bottom: 20px;
}
.bars,
.bar {
    fill: #add8e677;
  }


svg {
    display: block;
    margin: auto;
    background-color: #fafafa;
  }

